<template>
    <div class="container-fluid license-section" v-loading="loading">
        <h5 class="my-3">License / Support</h5>
        <div class="row">
            <div class="col-md-6">
                <el-card class="box-card">
                    <span class="mr-1">License</span>
                    <el-badge :value="valid ? 'Active' : 'Disabled'" class="item" :type="valid ? 'success' : 'danger'"></el-badge>
                    <hr>
                    <div class="container">
                        <div class="row">
                            <div class="col-12">
                                <p class="license-info">Enter your license and start enjoying the plugin premium features.</p>

                                <div class="row">
                                    <div class="col-8">
                                        <el-input :disabled="valid" size="small" v-model="license" placeholder="XXXXXXXXXXXXXXXXXXXXXX" />
                                    </div>
                                    <div class="col-4">
                                        <el-button @click="submitLicense('remove')" v-show="valid" type="danger" size="small" round>Remove</el-button>
                                        <el-button @click="submitLicense('activate')" v-show="!valid" type="primary" size="small" round>Activate</el-button>
                                    </div>
                                </div>
                                <hr>

                                <p class="license-info"><strong>Note:</strong> One plugin license per domain is allowed. For any issues with theme activation please refer to the plugin's doc. If registered elsewhere please deactivate that license or purchase a new one.</p>
                            </div>
                        </div>
                    </div>

                </el-card>
            </div>
            <div class="col-md-6">
                <el-card class="box-card">
                    <span class="mr-1">Premium Features</span>
                    <hr>
                    <div class="container-fluid">
                        <div class="row">
                            <div class="col-12 p-0 m-0">
                                <ul class="premium-bulletpoints">
                                    <li>- Unlimited Price List.</li>
                                    <li>- Create fixed discount Based on Price Lists.</li>
                                    <li>- Import Prices and Lists from CSV.</li>
                                    <li>- Premium support.</li>
                                    <li>- Updates.</li>
                                </ul>
                            </div>

                            <div class="col-12 mt-5 text-center">
                                <el-button type="danger" round><a href="https://www.webreadynow.com/product/wr-ultimate-price-list/" target="_blank">BUY A LICENSE</a></el-button>
                            </div>
                        </div>
                    </div>

                </el-card>
            </div>
        </div>

        <div class="row mt-5">
            <div class="col-4"></div>
            <div class="col-4 text-center">
                <a target="_blank" href="https://webreadynow.com/docs-category/wr-price-list-manager/">
                    <img :src="plugin_doc_url" alt="" width="50"><br><br>
                    Check the doc in case you have any doubt.
                </a>
            </div>
            <div class="col-4"></div>
        </div>

    </div>
</template>

<script>
    const axios = require('axios');
    import { ElMessage } from 'element-plus';
    import { DeleteFilled,Edit, InfoFilled, QuestionFilled } from '@element-plus/icons-vue';

    export default {
        components:{
            DeleteFilled, Edit, InfoFilled, QuestionFilled
        },
        data() {
            return {
                loading: false,
                license: '',
                valid: false,
                template_url: wrpl_params.plugin_url + '/uploads/wrpl_template.csv',
                plugin_doc_url: wrpl_params.plugin_url + '/assets/img/folder.png',
                locale: wrpl_params.locale
            }
        },
        computed:{

        },
        created(){
            this.getLicense();
        },
        methods:{
            submitLicense(type) {
                if(this.license.length === 22){
                    this.loading = true;
                    const formData = new FormData();
                    formData.append('action', 'wrpl_submit_license');
                    formData.append('license', this.license);
                    formData.append('type', type);
                    axios.post(wrpl_params.ajax_url, formData)
                        .then(response => {
                            if (response.data.success) {
                                this.price_lists = response.data.price_lists;
                                this.getLicense();
                                if(response.data.response.status == 'error'){
                                    ElMessage.error(response.data.response.message);
                                }else{
                                    ElMessage.success(response.data.response.message);
                                }

                                this.$emit('licenseChange');

                            } else {
                                ElMessage.error(this.locale.general_server_error);
                            }
                            this.loading = false;
                        })
                        .catch(error => {
                            ElMessage.error(error.message);
                            this.loading = false;
                        });
                }else{
                    ElMessage.error(this.locale.license_invalid_license)
                }
            },
            getLicense() {
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_license');
                axios.post(wrpl_params.ajax_url, formData)
                    .then(response => {
                        if (response.data.success) {
                            this.license = response.data.license;
                            this.valid = response.data.valid;
                        } else {
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch(error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            }
        }
    }
</script>

<style>
    p.license-info{
        font-size: 11px;
    }

    .premium-bulletpoints{
        padding-left: 0 !important;
    }

    .premium-bulletpoints li{
        font-size: 13px;
    }

    .license-section .el-card{
        min-height: 320px;
    }

    .license-section .el-card a{
        color: white;
        text-decoration: none;
    }

</style>
