<template>

    <div class="container-fluid roles " v-loading="loading">
        <h5 class="my-3">{{ this.locale.roles }}</h5>
        <p>{{this.locale.role_header_p}}</p>
        <div class="row pb-2">
            <div class="col-12">
                <el-button class="add-role-btn" @click="dialog= true" size="small" type="primary" plain><el-icon><plus /></el-icon> {{this.locale.add_role}}</el-button>
                <el-dialog
                        v-model="dialog"
                        :title="this.locale.add_role"
                        width="300px">

                    <el-input v-model="role_name" :placeholder="this.locale.role_name_required" />
                    <template #footer>
                      <span class="dialog-footer">
                        <el-button @click="dialog = false">{{this.locale.cancel}}</el-button>
                        <el-button type="primary" @click="addRole"
                        >{{this.locale.confirm}}</el-button>
                      </span>
                    </template>
                </el-dialog>
            </div>
        </div>
        <div class="row">
            <div class="col-12">
                <el-table :data="roles" style="width: 100%">
                    <el-table-column prop="name" :label="this.locale.role"  />
                    <el-table-column label="Price List" >
                        <template #default="scope">
                            <el-select size="small" v-model="scope.row.price_list" :placeholder="this.locale.select_price_list">
                                <el-option
                                        v-for="list in price_lists"
                                        :key="list.id"
                                        :label="list.description"
                                        :value="list.id"
                                />
                            </el-select>
                        </template>
                    </el-table-column>
                    <el-table-column width="115" align="right">
                        <template #default="scope">
                            <el-popconfirm v-if="scope.row.is_native"
                                    confirm-button-text="OK"
                                    :cancel-button-text="this.locale.no_thanks"
                                    :icon="InfoFilled"
                                    icon-color="red"
                                    :title="this.locale.role_pre_delete_notice"
                                    @confirm="removeRole(scope.row.id)"
                                    @cancel="">
                                <template #reference>
                                    <el-button size="small" type="primary" plain><el-icon><delete-filled /></el-icon></el-button>
                                </template>
                            </el-popconfirm>

                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <br>
        <p v-html="this.locale.role_footer_note"></p>
        <p>{{this.locale.role_footer_p}}</p>
        <el-button type="primary" @click="saveRelation">{{this.locale.save}}</el-button>

    </div>
</template>

<script>
    const axios = require('axios');
    import { ElMessage } from 'element-plus';
    import { DeleteFilled, InfoFilled, Plus } from '@element-plus/icons-vue';

    export default {
        components:{
            DeleteFilled, InfoFilled, Plus
        },
        data() {
            return {
                price_lists: [],
                roles: [],
                loading: false,
                dialog:false,
                role_name: '',
                locale: wrpl_params.locale
            }
        },
        computed:{

        },
        created(){
            this.getRoles();
            this.getPriceLists();
        },
        methods:{
            getRoles(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_all_roles');
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.roles = response.data.roles;
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            getPriceLists(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_price_lists');
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.price_lists = response.data.price_lists;
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            saveRelation(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_save_relation');
                formData.append('roles', JSON.stringify(this.roles));
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            ElMessage.success(this.locale.status_saved)
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            removeRole(role_id){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_remove_role');
                formData.append('role_id', role_id);
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            ElMessage.success(this.locale.general_server_error);
                            this.getRoles();
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            addRole(){
                if(this.role_name == ''){
                    ElMessage.error(this.locale.role_valid_name);
                    return;
                }
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_add_role');
                formData.append('name', this.role_name);
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            ElMessage.success(response.data.msg);
                            this.getRoles();
                            this.dialog = false;
                            this.role_name = "";
                        }else{
                            ElMessage.error(response.data.msg);
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            }
        }
    }
</script>

<style>
     .roles{
         font-size: 12px;
     }
    .add-role-btn{
        float: right;
    }
</style>
