<template>
    <div class="container-fluid import-section" v-loading="loading">
        <!--<h5 class="my-3">{{this.locale.import_header}}</h5>-->
        <div class="row">
            <div class="container">
                <div class="row mt-3">
                    <div class="col-md-6">
                        <h5>{{this.locale.import}}</h5>
                        <div class="col-12 mb-5">
                            <div>
                                <el-radio v-model="import_mode" label="1" size="large" border>{{this.locale.import_add_pl}}</el-radio>
                                <el-radio v-model="import_mode" label="2" size="large" border>{{this.locale.import_update_pl}}</el-radio>
                            </div>
                        </div>

                        <br>

                        <div class="col-12" v-if="import_mode === '1'">
                            <p class="mb-3">{{this.locale.import_new_pl_heading}}</p>
                            <el-input v-model="name" placeholder="Price List Name" />
                        </div>

                        <div class="col-12" v-if="import_mode === '2'">
                            <p class="mb-3">{{this.locale.import_select_pl_heading}}</p>
                            <el-select size="small" v-model="price_list.id" :placeholder="this.locale.select_price_list">
                                <el-option value="-1" :label="this.locale.select_price_list" />
                                <el-option v-for="list in getParentLists()"
                                           :key="list.id"
                                           :label="list.description"
                                           :value="list.id"
                                />
                            </el-select>
                        </div>

                        <div class="col-12 mt-4">
                            <input type="file"
                                   ref="file"
                                   @change="onFileChange(
                         $event.target.name, $event.target.files)">
                        </div>
                        <br>
                        <el-button @click="uploadCsv()" class="upload-csv" size="small" type="primary" plain>{{this.locale.import}}</el-button>
                    </div>
                    <div class="col-md-1"></div>
                    <div class="col-md-5">
                        <h5>{{this.locale.export}}</h5>
                        <p>{{this.locale.export_header}}</p>
                        <el-select size="small" class="export_list" v-model="price_list_export.id" :placeholder="this.locale.select_price_list">
                            <el-option value="-1" :label="this.locale.select_price_list" />
                            <el-option v-for="list in getParentLists()"
                                       :key="list.id"
                                       :label="list.description"
                                       :value="list.id"
                            />
                        </el-select>
                        <br>
                        <p class="mt-2" v-show="this.export_link != '' && !this.loading">Download the  <a :href="this.export_link">List</a></p>
                        <el-button @click="exportCsv()" class="export-csv mt-3" size="small" type="primary" plain>{{this.locale.export}}</el-button>


                    </div>
                </div>
            </div>


            <div class="col-12 mt-3">
                <p>{{this.locale.import_template_1}} (<a :href="template_url">test.csv</a>) {{this.locale.import_template_2}}</p>
            </div>

        </div>

        <div class="row mt-5" v-show="errors.length > 0">
            <div class="col-12 mb-2 text-center">
                {{this.locale.import_results}}
            </div>

            <div class="col-12 mt-2" style="height: 400px; overflow: scroll; background: white">
                <el-alert v-for="msg in errors" class="mt-1" :title="msg.msg" :type="msg.type" :closable="false" />

            </div>
        </div>

    </div>
</template>

<script>
    const axios = require('axios');
    import { ElMessage } from 'element-plus';
    import { DeleteFilled,Edit, InfoFilled, QuestionFilled } from '@element-plus/icons-vue';

    export default {
        components:{
            DeleteFilled, Edit, InfoFilled, QuestionFilled
        },
        data() {
            return {
                price_lists: [],
                price_list:{
                    description: '',
                    id: '-1',
                    id_parent: '0',
                    percent: 50
                },
                price_list_export:{
                    description: '',
                    id: '-1',
                    id_parent: '0',
                    percent: 50
                },
                export_link: '',
                loading: false,
                name: '',
                import_mode: '1',
                errors: [],
                template_url: wrpl_params.plugin_url + '/uploads/wrpl_template.csv',
                locale: wrpl_params.locale,
            }
        },
        computed:{

        },
        created(){
            this.getPriceLists();
        },
        methods:{
            getPriceLists(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_price_lists');
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.price_lists = response.data.price_lists;
                        }else{
                            ElMessage.error(this.locale.general_server_error);
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            getParentLists(){
                return this.price_lists.filter(list => parseInt(list.id_parent) == 0);
            },
            onFileChange(fieldName, file, caption) {
                const { maxSize } = this
                let csvFile = file[0];

                if (file.length>0) {
                    let size = csvFile.size / maxSize / maxSize
                    if (!csvFile.type.match('csv')) {
                        ElMessage.error(this.locale.csv_format_error);
                        this.$refs.file.value = null;
                    } else if (size>1) {
                        ElMessage.error(this.locale.csv_size_error);
                        this.$refs.file.value = null;
                    }
                }
            },
            uploadCsv(){
                if(this.name === '' && this.import_mode == '1'){
                    ElMessage.error(this.locale.price_list_required_name);
                    return;
                }

                if(this.price_list.id === '-1' && this.import_mode == '2'){
                    ElMessage.error(this.locale.select_price_list);
                    return;
                }
                if(this.$refs.file.value != null && this.$refs.file.value != ''){
                    this.loading = true;
                    const formData = new FormData();
                    formData.append('action', 'wrpl_import_csv');
                    formData.append('file', this.$refs.file.files[0]);
                    formData.append('import_mode', this.import_mode);
                    formData.append('name', this.name);
                    formData.append('price_list_id', this.price_list.id);
                    axios.post(wrpl_params.ajax_url, formData)
                        .then( response => {
                            if(response.data.success){
                                this.errors = response.data.msgs;
                                ElMessage.success(this.locale.upload_finished);

                                this.name = '';
                                this.price_list.id = '-1';
                                this.$refs.file.value = null;

                            }else{
                                ElMessage.error(response.data.msg)
                            }
                            this.loading = false;
                        })
                        .catch( error => {
                            ElMessage.error(error.message);
                            this.loading = false;
                        });
                }else{
                    ElMessage.error(this.locale.csv_empty_file_error);
                }
            },
            exportCsv(){
                if(this.price_list_export.id != '-1'){
                    this.loading = true;
                    const formData = new FormData();
                    formData.append('action', 'wrpl_export_csv');
                    formData.append('price_list_id', this.price_list_export.id);
                    axios.post(wrpl_params.ajax_url, formData)
                        .then( response => {
                            if(response.data.success){
                                //ElMessage.success(this.locale.upload_finished);
                                this.price_list_export.id = '-1';
                                this.export_link = response.data.path;
                            }else{
                                ElMessage.error(response.data.msg)
                            }
                            this.loading = false;
                        })
                        .catch( error => {
                            ElMessage.error(error.message);
                            this.loading = false;
                        });
                }else{
                    ElMessage.error(this.locale.select_price_list);
                }
            }
        }
    }
</script>

<style>
    .import-section .el-select--small, .import-section .el-input{
        width: 100% !important;
    }

</style>
