<template>

  <el-col class="container-fluid wrpl-admin-main p-0 m-0" v-loading="loading">
    <el-row class="tac">
      <el-col :span="4" class="wrpl-sidebar">
        <el-image
                style="width: 100%"
                :src="plugin_url + '/assets/img/logo.png'"
                :preview-src-list="srcList"
                :initial-index="4"
                fit="contain"

        />
        <hr>
        <el-menu
                default-active="1"
                class="el-menu-vertical-main"
        >

          <el-menu-item index="1" @click="changeScreen('products')">
            <el-icon><shop /></el-icon>
            <span>{{this.locale.products}}</span>
          </el-menu-item>
          <el-menu-item index="2" @click="changeScreen('price_lists')">
            <el-icon><list /></el-icon>
            <span>{{this.locale.price_list_header}}</span>
          </el-menu-item>
          <el-menu-item index="3" @click="changeScreen('roles')">
            <el-icon><user-filled /></el-icon>
            <span>{{this.locale.roles}}</span>
          </el-menu-item>


          <el-menu-item :disabled="!valid" index="4" @click="changeScreen('import')">
            <el-icon><download /></el-icon>
            <span>{{this.locale.import_menu}}</span>
            <el-badge value="PRO" v-if="!valid" class="item pro-badge" size="small" type="warning"></el-badge>
          </el-menu-item>


          <el-menu-item index="5" @click="changeScreen('settings')">
            <el-icon><setting /></el-icon>
            <span>{{this.locale.settings_header}}</span>
          </el-menu-item>

          <el-menu-item index="6" @click="changeScreen('license')">
            <el-icon><help-filled /></el-icon>
            <span>{{this.locale.license_support_menu}}</span>
          </el-menu-item>

        </el-menu>
      </el-col>
      <el-col :span="20" class="main-area">

          <Products v-if="active_screen === 'products'" @changeLoading="changeLoading"></Products>
          <Roles v-if="active_screen === 'roles'" ></Roles>
          <PriceLists :valid="valid" v-if="active_screen === 'price_lists'"></PriceLists>
          <Import v-if="active_screen === 'import'"></Import>
          <Settings v-if="active_screen === 'settings'"></Settings>
          <License @licenseChange="licenseChange" v-if="active_screen === 'license'"></License>

    </el-col>
  </el-row>
  </el-col>
</template>

<script>
  const axios = require('axios');
  import { Setting,Shop, List, Plus, UserFilled, Download, HelpFilled } from '@element-plus/icons-vue';
  import { ElMessage } from 'element-plus';
  import Products from "./Products.vue";
  import Roles from "./Roles.vue";
  import PriceLists from "./PriceLists.vue";
  import Import from "./Import.vue";
  import Settings from "./Settings.vue";
  import License from "./License.vue";

  export default {
    components:{
      Setting, Shop, List, Plus, UserFilled, Download, HelpFilled, Products, Roles, PriceLists, Import, Settings, License
    },
    data() {
      return {
        loading: false,
        plugin_url: wrpl_params.plugin_url,
        ajax_url: wrpl_params.ajax_url,
        locale: wrpl_params.locale,
        active_screen: 'products',
        valid: false,
      }
    },
    computed:{

    },
    created(){
      this.getLicense();
    },
    methods:{
      changeScreen(screen){
        this.active_screen = screen;
      },
      changeLoading(flag){
        this.loading = flag;
      },
      getLicense() {
        this.loading = true;
        const formData = new FormData();
        formData.append('action', 'wrpl_get_license');
        axios.post(wrpl_params.ajax_url, formData)
                .then(response => {
                  if (response.data.success) {
                    this.valid = response.data.valid;
                  } else {
                    ElMessage.error(this.locale.license_error)
                  }
                  this.loading = false;
                })
                .catch(error => {
                  ElMessage.error(error.message);
                  this.loading = false;
                });
      },
      licenseChange(){
        this.getLicense();
      }
    }
  }
</script>

