  #wrpl-app{
    background: #eef1f5 !important;
    height: 100vh;
  }

  .wrpl-sidebar, .wrpl-admin-main, .wrpl-admin-main > .el-row, .wrpl-sidebar > .el-menu{
    height: 100%;
  }

  .el-table__body, #wrpl-app{
    font-family: 'Poppins' !important;

  }

  .el-table__body .cell{
    font-size: 11px;
  }

  #wpfooter{
    display: none;
  }
  #wpbody-content{
    padding-bottom: 0 !important;
  }

  .wrpl-sidebar{
    background: white;
  }

  .wrpl-sidebar img{
    display: block !important;
    margin: 10px auto 0px;
    height: 80px !important;
    width: 90% !important;
  }

  .wrpl-sidebar hr{
    margin: 0 !important;
  }

  .wrpl-sidebar span{
    font-size: 13px;
  }

  .wrpl-sidebar .el-menu{
    border: 0;
  }

  .pro-badge .el-badge__content{
    top: -28px !important;
  }

</style>
