<template>
    <div class="container-fluid price_lists" v-loading="loading">
        <h5 class="my-3">{{ this.locale.price_list_header }}</h5>
        <div class="row">
            <div class="col-12">
                <el-button class="add-price-list-btn" @click="openDialog" size="small" type="primary" plain><el-icon><plus /></el-icon> {{ this.locale.add_pl  }}</el-button>
                <el-dialog
                        v-model="dialog"
                        :title=" edit ? this.locale.update_list : this.locale.add_pl"
                        width="300px">

                    <p class="info-field">Name:</p>
                    <el-input v-model="price_list.description" :placeholder="this.locale.pl_name_modal_placeholder" />

                    <el-tooltip
                            class="box-item"
                            placement="top-start">
                        <template #content>
                            {{this.locale.pl_copy_from_tooltip}}
                        </template>
                        <p class="info-field mt-3"><el-icon><question-filled /></el-icon> {{this.locale.pl_percent_label}} <el-badge v-if="!valid" value="PRO" class="item" size="small" type="warning"></el-badge></p>

                    </el-tooltip>

                    <el-select :disabled="!valid" class="select-pl" size="small" v-model="price_list.id_parent">
                        <el-option value="0" label="No Parent" />
                        <el-option
                                v-for="list in getParentLists()"
                                :key="list.id"
                                :label="list.description"
                                :value="list.id"
                        />
                    </el-select>

                    <el-tooltip
                            class="box-item"
                            placement="top-start">
                        <template #content >
                            <p v-html="this.locale.pl_percent_tooltip"></p>
                        </template>
                        <p class="info-field mt-3"><el-icon><question-filled /></el-icon>  {{this.locale.percent}} (%):</p>
                    </el-tooltip>
                    <el-input-number class="factor-field" :precision="1" min="1" :disabled="price_list.id_parent === '0' || !valid" v-model="price_list.percent" placeholder="" />

                    <template #footer>
                      <span class="dialog-footer">
                        <el-button @click="dialog = false">{{this.locale.cancel}}</el-button>
                        <el-button type="primary" @click="addPriceList"
                        >{{this.locale.confirm}}</el-button>
                      </span>
                    </template>
                </el-dialog>
            </div>
        </div>
        <div class="row my-3">
            <div class="col-12">
                <el-table :data="price_lists" style="width: 100%">
                    <el-table-column prop="description" :label="this.locale.pl_table_columns.name"  />
                    <el-table-column align="right" :label="this.locale.pl_table_columns.actions">

                        <template #default="scope">
                            <!--<el-button v-if="scope.row.id != 1" @click="updateList(scope.row)" size="small" type="primary" plain><el-icon><edit /></el-icon></el-button>-->
                            <el-popconfirm
                                    v-if="scope.row.id != 1"
                                    confirm-button-text="OK"
                                    :cancel-button-text="this.locale.no_thanks"
                                    :icon="InfoFilled"
                                    icon-color="red"
                                    :title="this.locale.pl_pre_delete_notice"
                                    @confirm="deletePriceList(scope.row.id)"
                                    @cancel="">
                                <template #reference>
                                    <el-button size="small" type="primary" plain><el-icon><delete-filled /></el-icon></el-button>
                                </template>
                            </el-popconfirm>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>

<script>
    const axios = require('axios');
    import { ElMessage } from 'element-plus';
    import { DeleteFilled,Edit, InfoFilled, QuestionFilled } from '@element-plus/icons-vue';

    export default {
        components:{
            DeleteFilled, Edit, InfoFilled, QuestionFilled
        },
        props: ['valid'],
        data() {
            return {
                price_lists: [],
                price_list:{
                    description: '',
                    id: -1,
                    id_parent: '0',
                    percent: 100
                },
                loading: false,
                dialog: false,
                edit: false,
                locale: wrpl_params.locale
            }
        },
        computed:{

        },
        created(){
            this.getPriceLists();
        },
        methods:{
            getPriceLists(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_price_lists');
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){

                            this.price_lists = response.data.price_lists;
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            addPriceList(){
                if(this.price_list.description == ''){
                    ElMessage.error(this.locale.price_list_required_name);
                    return;
                }
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_add_price_list');
                formData.append('name', this.price_list.description);
                formData.append('id_parent', this.price_list.id_parent);
                formData.append('percent', this.price_list.percent);
                formData.append('edit', this.edit);
                formData.append('id', this.price_list.id);
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.getPriceLists()
                            ElMessage.success(response.data.msg);
                            this.dialog = false;
                            this.price_list = {
                                description: '',
                                id_parent: '0',
                                percent: 50,
                                id: -1,
                            }
                        }else{
                            ElMessage.error(response.data.msg)
                        }
                        this.loading = false;

                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            deletePriceList(id){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_delete_price_list');
                formData.append('id', id);
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.getPriceLists()
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            updateList(list){
                this.price_list = {
                    description: list.description,
                    id_parent: list.id_parent,
                    percent: list.percent,
                    id: list.id
                }
                this.dialog = true;
                this.edit = true;


            },
            getParentListName(list_id){
               let list = this.price_lists.find(list => parseInt(list.id) === parseInt(list_id));
               return list ?  list.description : '-';
            },
            getParentLists(){
                return this.price_lists.filter(list => parseInt(list.id_parent) === 0 && list.id != this.price_list.id);
            },
            getFactor(factor){
                return factor ? factor : '-';
            },
            getPercent(number){
               return parseFloat(number).toFixed(2);
            },
            openDialog(){
                this.edit = false
                this.dialog = true;
                this.price_list = {
                    description: '',
                    id_parent: '0',
                    percent: 50,
                    id: -1,
                }
            }
        }
    }
</script>

<style>
    .price_lists{
        font-size: 12px;
    }

    .add-price-list-btn{
        float: right;
    }

    .select-pl, .factor-field{
        width: 100% !important;
    }

    .info-field{
        font-size: 11px;
        width: fit-content !important;
        margin-bottom: 0 !important;
    }

    .percent-cell.above{
        color: green;
    }

    .percent-cell.below{
        color: red;
    }

</style>
