<template>

    <div class="container-fluid" >
        <div class="row my-3">
            <div class="col-5">
                <el-select  class="price-list-select" size="small" @change="getProducts" v-model="price_list" placeholder="Select Price List">
                   <!-- <el-option :key="1"  label="Default Woocommerce" :value="1"/>-->
                    <el-option
                            v-for="list in price_lists"
                            :key="list.id"
                            :label="list.description"
                            :value="normalizeInt(list.id)"
                    />
                </el-select>
            </div>
            <div class="col-5">
                <el-select size="small" @change="getProducts" v-model="category" placeholder="Select">
                    <el-option :key="0" :value="0" :label="this.locale.all_categories" />
                    <el-option
                            v-for="category in categories"
                            :key="category.term_id"
                            :label="replaceSpecialCharacters(category.text)"
                            :value="category.term_id"
                    />
                </el-select>
            </div>
            <div class="col-2">
                <el-input @change="getProducts" v-model="search" size="small" :placeholder="this.locale.search_placeholder" />
            </div>
        </div>
        <div class="row">
            <!-- Product Table -->
            <div class="col-12">
                <el-table ref="productsTable" :data="products" :max-height="680" style="width: 100%" v-loading="tableLoading">
                    <el-table-column width="70" :label="this.locale.product_table_columns.id" prop="ID" >
                        <!--<template #default="props">
                            <p>State: {{ props.row.post_title }}</p>
                            <p>City: {{ props.row.post_title }}</p>
                            <p>Address: {{ props.row.post_title }}</p>
                            <p>Zip: {{ props.row.post_title }}</p>
                        </template>-->
                    </el-table-column>
                    <el-table-column label="" prop="image" width="60">
                        <template #default="scope">
                            <el-image
                                    style="width: 30px; height: 30px"
                                    :src="scope.row.image ? scope.row.image : ''"
                                    fit="cover">
                                <template #error>
                                    <div class="image-slot">
                                        <div class='square' style='height: 25px;width: 25px;background-color: #555;'></div>
                                    </div>
                                </template>
                            </el-image>
                        </template>

                    </el-table-column>
                    <el-table-column :label="this.locale.product_table_columns.name" prop="post_title" />
                    <el-table-column width="100" :label="this.locale.product_table_columns.sku" prop="sku" />
                    <el-table-column :label="this.locale.product_table_columns.category" prop="categories" />
                    <el-table-column :label="this.locale.product_table_columns.type" width="85" prop="post_type" >
                        <template #default="scope">
                            <el-badge type="primary" :value="productType(scope)" ></el-badge>
                        </template>
                    </el-table-column>
                    <el-table-column :label="this.locale.product_table_columns.price" class="grouping-price-column">
                        <el-table-column class-name="price-column"  :label="this.locale.product_table_columns.regular" width="110" prop="price" >
                            <template #default="scope">
                                <el-input-number v-if="scope.row.type === 'simple' || scope.row.type === 'variation'" size="small" @change="updatePrice(scope.row, 1)" step="0.01" precision="2" min="0"  v-model="scope.row.price" controls-position="right"/>
                                <span v-else>-</span>
                            </template>
                        </el-table-column>
                        <el-table-column class-name="price-column"  width="110" :label="this.locale.product_table_columns.sale" prop="sale_price" >
                            <template #default="scope">
                                <el-input-number v-if="scope.row.type === 'simple' || scope.row.type === 'variation'"  size="small" @change="updatePrice(scope.row,2)" step="0.01" precision="2" min="0" v-model="scope.row.sale_price" controls-position="right"/>
                                <span v-else>-</span>
                            </template>
                        </el-table-column>
                    </el-table-column>
                    <!--<el-table-column v-if="current_list.id_parent != '0'" class-name="price-column"  label="Price" width="80" prop="price" >
                        <template #default="scope">
                            <span >{{ priceFormat(scope.row.price) }}</span>
                        </template>
                    </el-table-column>-->
                        <!--<el-table-column class-name="price-column"  width="110" label="Sale" prop="sale_price" >
                            <template #default="scope">
                                <span>{{scope.row.sale_price}}</span>
                            </template>
                        </el-table-column>-->

                    <el-table-column width="115" align="right">
                        <!--<template #header>
                            <el-input @change="getProducts" v-model="search" size="small" placeholder="Type to search" />
                        </template>-->
                        <template #default="scope">
                            <el-button class="show-icon" size="small" type="primary" plain @click="goAction(scope.row, 'view')">
                                <svg viewBox="0 0 1024 1024" xmlns="http://www.w3.org/2000/svg" data-v-ba633cb8=""><path fill="#409eff" d="M512 160c320 0 512 352 512 352S832 864 512 864 0 512 0 512s192-352 512-352zm0 64c-225.28 0-384.128 208.064-436.8 288 52.608 79.872 211.456 288 436.8 288 225.28 0 384.128-208.064 436.8-288-52.608-79.872-211.456-288-436.8-288zm0 64a224 224 0 1 1 0 448 224 224 0 0 1 0-448zm0 64a160.192 160.192 0 0 0-160 160c0 88.192 71.744 160 160 160s160-71.808 160-160-71.744-160-160-160z"></path></svg>
                            </el-button>
                            <el-button size="small" type="primary" plain @click="goAction(scope.row,  'edit')"><el-icon><edit /></el-icon></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="row mt-3">
            <div class="col-3">
                <span class="pagination-footer-count-text">{{ this.showing() }}</span>
            </div>
            <div class="col-6">
                <div class="product-footer-pagination">
                    <el-pagination
                            layout="prev, pager, next"
                               v-model:current-page="currentPage"
                               :page-size="length"
                               :total="productTotal"
                                @current-change="changePage"
                    />
                </div>
            </div>
            <div class="col-3">
                <el-select size="small" class="page-length" @change="getProducts" v-model="length" placeholder="Select">
                    <el-option :key="10" :value="10" label="10" />
                    <el-option :key="15" :value="15" label="15" />
                    <el-option :key="20" :value="20" label="20" />
                </el-select>
            </div>
        </div>
    </div>
</template>

<script>
    const axios = require('axios');
    import { ElMessage } from 'element-plus';
    import { View,Edit } from '@element-plus/icons-vue';
    import $ from "jquery";

    export default {
        components:{
            Edit,View
        },
        data() {
            return {
                search: '',
                length: 10,
                category: 0,
                products:[],
                productTotal: 0,
                currentPage: 1,
                categories: [],
                price_lists:[],
                tableLoading: false,
                price_list: 1,
                ajax_url: wrpl_params.ajax_url,
                locale: wrpl_params.locale,
                /*current_list : {
                    id: 1,
                    id_parent:0,
                    parent: {
                        description: ''
                    }
                }*/
            }
        },
        computed:{

        },
        created(){
            this.getProducts();
            this.getCategories();
            this.getPriceLists();
        },
        methods:{
            getProducts(){
                this.tableLoading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_products');
                formData.append('price_list', this.price_list);
                formData.append('category_id', this.category);
                formData.append('start', (this.currentPage -1) * this.length);
                formData.append('length', this.length);
                formData.append('search', this.search);

                axios.post(this.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.products = response.data.data;
                            this.productTotal = response.data.recordsFiltered;
                            //this.current_list = this.price_lists.find(list => parseInt(list.id) === parseInt(this.price_list));
                            //this.current_list.parent = this.price_lists.find(list => parseInt(list.id) === parseInt(this.current_list.id_parent));
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }

                        this.tableLoading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.tableLoading = false;
                    });
            },
            getCategories(){
                const formData = new FormData();
                formData.append('action', 'wrpl_get_categories');
                axios.post(this.ajax_url, formData)
                    .then( response => {
                        this.categories = response.data;

                        //todo => put a success flag here to check if the request went good
                        /*if(response.data.success){

                        }else{
                            ElMessage.error('There was an error trying to get the categories')
                        }*/
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                    });
            },
            showing(){
              var length = this.productTotal < this.length ? this.productTotal : this.length;

              return this.locale.showing + " " + length + ' ' +  this.locale.of + ' ' + this.productTotal + ' ' + this.locale.products;
            },
            getPriceLists(){
                const formData = new FormData();
                formData.append('action', 'wrpl_get_price_lists');
                axios.post(this.ajax_url, formData)
                    .then( response => {
                       if(response.data.success){
                           this.price_lists = response.data.price_lists;
                       }else{
                           ElMessage.error(this.locale.general_server_error)
                       }
                    })
                    .catch( error => {
                    ElMessage.error(error.message);
                });
            },
            getCurrentPriceList(){

            },
            updatePrice(row, type){
                this.tableLoading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_edit_price');
                formData.append('price_list', this.price_list);
                formData.append('price', row.price);
                formData.append('sale_price', row.sale_price);
                formData.append('id', row.ID);
                axios.post(this.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            //Error, we need to get back the original values
                            if(row.price <= row.sale_price && (parseInt(row.price) + parseInt(row.sale_price) != 0 ) ){

                                if(type === 1){
                                    row.price = response.data.price;
                                }else{
                                    row.sale_price = response.data.sale_price;
                                }

                                this.$refs.productsTable.doLayout();
                                ElMessage.error(response.data.msg);
                            }
                        }else{
                            ElMessage.error(this.locale.price_not_updated)
                        }
                        this.tableLoading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.tableLoading = false;
                    });
            },
            goAction(row, action){
                if(action === 'view'){
                    window.open(
                        row.guid,
                        '_blank' // <- This is what makes it open in a new window.
                    );

                }else{
                    window.open(
                        row.edit_url,
                        '_blank' // <- This is what makes it  open ina new window.
                    );
                }
            },
            changePage(){
                this.getProducts()
            },
            changeLoading(flag){
                this.$emit('changeLoading',flag)
            },
            replaceSpecialCharacters(str){
                return str.replace('&amp;', '&');
            },
            normalizeInt(number){
                return parseInt(number);
            },
            priceFormat(number){
                return number.toFixed(2);
            },
            productType(scope){
                if(scope.row.post_type === 'product_variation'){
                    return this.locale.product_table_columns.variation;
                }else{
                    if(scope.row.type === 'external'){
                        return this.locale.external
                    }

                    if(scope.row.type === 'grouped'){
                        return this.locale.grouped
                    }
                    return this.locale.simple
                }
            }
        }
    }
</script>

<style>

    .el-table_1_column_7 .cell{
        text-align: center;
    }

    .el-input__wrapper{
        padding: 0 !important;
    }
    th .cell{
        font-size: 11px;
    }
    .product-footer-pagination{
        display: flex;
        justify-content: center;
        align-content: center;
    }

    .btn-prev, .btn-next{
        margin-top: -6px !important;
    }

    .el-input__inner{
        background: white !important;
    }

    .pagination-footer-count-text{
        font-size: 12px;
    }

    .el-badge__content{
        font-size: 10px !important;
    }

    .price-column .el-input-number__decrease, .price-column .el-input-number__increase{
        display: none !important;
    }

    .price-column > .cell > .el-input-number{
        width: 80px !important;
    }

    .page-length{
        width: 65px;
        float: right;
    }

    .show-icon svg{
        height: 14px !important;
    }

    .price-list-select{
        width: 280px;
    }

</style>
