<template>
    <div class="container-fluid settings-section" v-loading="loading">
        <h5 class="my-3">Settings</h5>
        <div class="row">
            <div class="col-12" >
                <p class="mb-3">{{this.locale.settings_header_p}}</p>
                <el-select size="small" v-model="price_list.id" :placeholder="this.locale.select_price_list">
                    <el-option v-for="list in price_lists"
                               :key="list.id"
                               :label="list.description"
                               :value="list.id"
                    />
                </el-select>
            </div>

<!--            <div class="col-12 mt-5" >
                <p class="mb-3">{{this.locale.settings_price_format_heading}}</p>
                <div>
                    <el-radio v-model="price_format" label="1" size="small">{{this.locale.settings_price_format_1}} (<del>$4.25</del> $4.00)</el-radio>
                    <el-radio v-model="price_format" label="2" size="small">{{this.locale.settings_price_format_2}} ($4.00 <del>$4.25)</del></el-radio>
                    <el-radio v-model="price_format" label="3" size="small">{{this.locale.settings_price_format_3}} ($4.00)</el-radio>
                </div>
            </div>-->

            <div class="col-12 mt-5" >
                <p class="mb-3">{{this.locale.settings_message_unregistered_heading}}</p>
                <div class="row">
                    <div class="col-md-2">
                        <el-checkbox v-model="hide_prices"  :label="this.locale.settings_hide_label" size="small" />
                    </div>
                    <div class="col-md-10">
                        <textarea :disabled="!hide_prices" v-model="hide_message"></textarea>
                    </div>
                </div>
            </div>

            <div class="col-12 mt-3">
                <el-button @click="saveSettings()" size="small" type="primary" plain>{{this.locale.save}}</el-button>
            </div>

        </div>
    </div>
</template>

<script>
    const axios = require('axios');
    import { ElMessage } from 'element-plus';
    import { DeleteFilled,Edit, InfoFilled, QuestionFilled } from '@element-plus/icons-vue';

    export default {
        components:{
            DeleteFilled, Edit, InfoFilled, QuestionFilled
        },
        data() {
            return {
                price_lists: [],
                price_list:{
                    description: '',
                    id: '1',
                    id_parent: '0',
                    percent: 50
                },
                loading: false,
                price_format: '1',
                hide_prices: false,
                hide_message: '',
                locale: wrpl_params.locale
            }
        },
        computed:{

        },
        created(){
            this.getPriceLists();
            this.getSettings();
        },
        methods:{
            getPriceLists(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_price_lists');
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.price_lists = response.data.price_lists;
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            saveSettings(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_save_settings');
                formData.append('price_format', this.price_format);
                formData.append('hide_prices', this.hide_prices ? 1 : 0);
                formData.append('hide_message', this.hide_message);
                formData.append('price_list_id', this.price_list.id);
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            ElMessage.success(response.data.msg);
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            },
            getSettings(){
                this.loading = true;
                const formData = new FormData();
                formData.append('action', 'wrpl_get_settings');
                axios.post(wrpl_params.ajax_url, formData)
                    .then( response => {
                        if(response.data.success){
                            this.price_list.id = response.data.settings.price_list_id;
                            this.price_format = response.data.settings.price_format;
                            this.hide_message = response.data.settings.hide_message;
                            this.hide_prices = response.data.settings.hide_prices;
                        }else{
                            ElMessage.error(this.locale.general_server_error)
                        }
                        this.loading = false;
                    })
                    .catch( error => {
                        ElMessage.error(error.message);
                        this.loading = false;
                    });
            }
        }
    }
</script>

<style>

    .settings-section .el-select--small, .settings-section textarea{
        width: 400px !important;
        font-size: 12px;
    }

    .settings-section textarea{
        height: 150px;
    }

</style>
